import common from '../../../util/common';
import request from '../../../util/request';

export default {
    name: "Mine",
    data() {
        return {
            userInfo: this.$store.state.userInfo,
            shareModal: false,
            clipboard: '我正在用畅游看吧读免费百万小说。下载地址：http://www.xxx.com',
            displayInfo: [
                {
                    icon: 'appreciate',
                    title: '请给我好评'
                },
                {
                    icon: 'share',
                    title: '分享给朋友'
                },
                {
                    icon: 'comment',
                    title: '意见反馈'
                },
                {
                    icon: 'settings',
                    title: '设置'
                }
            ]
        }
    },
    computed: {
        sexClazz() {
            let clazz = '';
            if (this.userInfo.gender) {
                if (this.userInfo.gender === '男') {
                    clazz = 'cuIcon-male bg-blue';
                } else if (this.userInfo.gender === '女') {
                    clazz = 'cuIcon-female bg-pink';
                }
            }
            return clazz;
        },
        avatarClazz() {
            let clazz = 'background-image:url(\'data:image/svg+xml;base64,\');';
            if (this.userInfo.avatar) {
                clazz = `background-image:url(${this.userInfo.avatar})`;
            }
            return clazz;
        }
    },
    onPullDownRefresh() {
        console.error('下拉mine');
        uni.stopPullDownRefresh();
    },
    methods: {
        tapBtn(type) {
            switch (type) {
                case 'appreciate':
                    break;
                case 'share':
                    this.shareModal = true;
                    break;
                case 'comment':
                    uni.navigateTo({
                        url: '/pages/comment/Comment'
                    });
                    break;
                case 'settings':
                    break;
            }
        },
        hideShareModal(type) {
            this.shareModal = false;
            if (!type) {
                // 复制到剪切板
                uni.setClipboardData({
                    data: this.clipboard,
                    success: function () {
                        console.log('success');
                    }
                });
            }
        },
        loginWx() {
            if (!this.userInfo) {
                uni.login({
                    success: response2 => {
                        debugger;
                        // 获取用户信息
                        uni.getUserInfo({
                            provider: 'weixin',
                            success: response3 => {
                                debugger;
                                uni.showLoading({
                                    title: '登陆中'
                                });
                                let params = {
                                    condition: {
                                        code: response2.code,
                                        avatar: response3.userInfo.avatarUrl,
                                        nickName: response3.userInfo.nickName,
                                        gender: common.getGender(response3.userInfo.gender)
                                    }
                                };
                                request.post('/users/weixin/getUsersInfo', params).then(data => {
                                    uni.hideLoading();
                                    if (data.status === 200) {
                                        let result = JSON.stringify(data.data[0]);
                                        uni.setStorage({
                                            key: 'userInfo',
                                            data: result
                                        });
                                        this.$store.commit('SET_USERINFO', data.data[0]);
                                        this.userInfo = data.data[0];
                                    } else {
                                        uni.showToast({
                                            title: '获取用户信息失败',
                                            duration: 1000,
                                            icon: 'none'
                                        });
                                    }
                                }).catch(e => {
                                    uni.hideLoading();
                                    uni.showToast({
                                        title: '获取用户信息失败',
                                        duration: 1000,
                                        icon: 'none'
                                    });
                                })
                            },
                            fail: reject3 => {
                                debugger;
                                // doNothing...
                            }
                        });
                    },
                    fail: response2 => {
                        debugger;
                        uni.showToast({
                            title: '登录失败',
                            duration: 1000,
                            icon: 'none'
                        });
                    }
                });
            }
        }
    }
}
